.footer {
    background-color: var(--container-color);
    border-top: 1px solid rgba(0, 0, 0, .1);
}

.footer__container {
    padding: 2rem 0 6rem;
}

.footer__title, .footer__link {
    color: var(--title-color);
}

.footer__title {
    font-size: var(--h3-font-size);
    text-align: center;
    margin-top: var(--mb-2);
    margin-bottom: var(--mb-2);
}

.footer__link:hover {
    color: var(--title-color-dark);
}

.footer__list {
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);
}

.footer__social {
    display: flex;
    justify-content: center;
    column-gap: 1.25rem;
}

.footer__social-link {
    background-color: var(--title-color);
    color: var(--container-color);
    font-size: 1.25rem;
    padding: .4rem;
    border-radius: .5rem;
    display: inline-flex;
    cursor: pointer;
}